var that;
export default {
	data() {
		return {
			list: [],
			tabCur: 0,
			mainCur: 0,
			verticalNavTop: 0,
			load: true
		};
	},
	onLoad() {
		uni.showLoading({
			title: '加载中...',
			mask: true
		});
	},
	onReady() {
		uni.hideLoading();
	},
	methods: {
		TabSelect(e) {
			this.tabCur = e.currentTarget.dataset.id;
			this.mainCur = e.currentTarget.dataset.id;
			this.verticalNavTop = (e.currentTarget.dataset.id - 1) * 50;
		},
		tpinfo(e) {
			let url = e.currentTarget.dataset.url;
			uni.navigateTo({
				url: url
			});
		},
		getlist() {
			that.$request.request({
				url: that.api.default.cat_list
			}).then(res=>{
				console.log(res);
				that.list=res.list
			}) 
		}
	},
	onLoad() {
		that = this;
		that.getlist();
	}
};
